<template>
    <div>
        <div class="sass">
            <div class="sass__top">这是测试用例</div>
            <div class="sass__color">
                <div class="sass__mix">mix混合色</div>
                <div class="sass__unquote">从字符串中删除引号</div>
                <div class="sass__quote">从字符串中加上引号</div>
                <div class="sass__length">字符串长度</div>
                <div class="sass__strIndex">找到某个字段的位置</div>
                <div class="sass__strSlice">从某个字段里截取子字段</div>
            </div>
            <div class="sass__list">
                <div class="list--length">list长度</div>
                <div class="list--nth">list的具体某个位置</div>
            </div>
            <div class="sass__map">
                <div class="map--keyword">keyword关键字</div>
            </div>
            <div class="sass__selector">
                <div class="selector-nest">selector-nest</div>
                <div class="selector-append">selector-append</div>
            </div>
            <router-link to="/talking">到talking页面</router-link>
            <div>{{num}}</div> 
            <button id="pro_id" v-track-event="{category:'button', action:'click'}" @click="clickDemo">点击事件</button>      
        </div>
    </div>
</template>
<script>
export default {
  name: 'sass',
  data(){
    return{
        num:2
    }
  },
  methods:{
    clickDemo(){
        ++this.num
    }
  }
}
</script>

<style lang="scss">
  @import '../../assets/css/common/common.scss';
  .sass{
    font: {
        family: '微软雅黑';
        size: 20px;
        weight: bold;
    }
    color: $colora;
    background-color: $bgColor;
    .sass__color{
        .sass__mix{
            background-color: $mixColor;
        }
        .sass__unquote{
            content: $unquote;
        }
        .sass__quote{
            content: $quote;
        }
        .sass__length{
            content: $stringLength;
        }
        .sass__strIndex{
            content: $strIndex;
        }
        .sass__strSlice{
            content: $strSlice;
        }
    }
    .sass__list{
        .list--length{
            content: $list-length;
        }
        .list--nth{
            content: $nth;
        }
    }
    .sass__map{
        .map--keyword{
            @include foo(
                $dribble: #ea4c89,
                $facebook: #3b5998,
                $github: #171515,
                $google: #db4437,
                $twitter: #55acee
            )
        }
    }
    .sass__selector{
        .selector-nest{
            content: $selector-nest;
        }
        .selector-append{
            content: $selector-append;
        }
    }
  }
</style>
